import {Component, OnInit} from "@angular/core";
import {AlertController, IonicPage, NavController, NavParams } from "ionic-angular";
import {SceneData} from '../../providers';

@IonicPage()
@Component({
  selector: 'page-blog',
  templateUrl: 'blog.html',
})
export class Blog implements OnInit {

  blog;
  blogIndex: number;
  scene: any = {};

  constructor(public navParams: NavParams,
              public navCtrl: NavController,
              public sceneData: SceneData,
              public alertCtrl: AlertController) {}

  ngOnInit() {
    this.blog = this.navParams.get('blog');
    this.blogIndex = this.navParams.get('index');
    this.sceneData.getScene().then((data: any) => {
      this.scene = data;
      if (!this.scene.language) this.scene.language = 0;
      this.sceneData.structuredGroups();
    });
  }

  openGroup(group: any) {
    this.sceneData.openGroup(group);
  }

  openLink(url: string) {
    this.sceneData.openLink(url);
  }

  sendForm(form: string) {
    this.sceneData.sendForm(form);
  }

}



